.input{
    border: none;
    border-radius: 5px;
    font-size: 20px;
    outline: #1a95f0;
    transition: .2s ease-in-out;
    padding: 10px 20px;
    &:focus {
        outline: 2px solid #1a95f0;
    }&.input-erro{
        outline: 2px solid red;
    }
}

.label{
    font-size: 20px;
}